h1 {
  margin:0;
}

h2 {
  margin:0;
}



.App {
  width: 100vw;
  height: 100vh;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  caret-color: transparent;
  border-style: none;
}

.Header {
  background-color: #fbfdff;
  color:#000000;
  border-style:outset;
  border-bottom-width: 1px;
  border-bottom-color: #d5e3eb;
  padding-left: 15%;
  padding: 0;
  margin: 0;
  font-size: 36;
  vertical-align:top;
}


.ContentPane {
  background-color: #f7fcff;
  color: #1B1B1E;
  padding: 0;
  margin: 0;
  font-size: 32;
  vertical-align:top;
  display: flex;
  height: 100%;
  width:100%;
  flex-direction: column;
}

.ContentPaneBody {
  background-color: #f7fcff;
  color: #1B1B1E;
  height: 100%;
  width: 100%;
}

.Content {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.SidebarContainer {
  height: 100%;
  width: 15%;
  margin: 0;
  padding: 0;
  background-color: #8d8d8d;
  font-size: 24px;
  vertical-align:top;
  display: flex;
  flex-direction: row;
}

.SidebarButton {
  height: 50px;
  width: 50px;
  margin: 0;
  padding: 4px;
  color: #fbfffe;
  font-size: 24px;
  display: flex;
}

.Sidebar {
  height: auto;
  width: 100%;
  margin: 0;
  padding: 0;
  color: #fbfffe;
  font-size: 24px;
  display: flex;
  flex-direction: column;
}

.SidebarList {
  height: auto;
  padding:0;
  margin:0;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.SidebarList#hidden{
  height: auto;
  padding:0;
  margin:0;
  width: 100%;
  display: flex;
  flex-direction: column;
  visibility: hidden;
}

.SidebarList .ListItem#active {
  background-color: #FAA916;
  color: #1B1B1E;
}

.SidebarList .ListItem a {
  width: 100%;
  height: 50px;
  margin-left: 15px;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  color:#fbfffe;
  text-decoration: none;
}

.SidebarList .ListItem:hover {
  cursor: pointer;
  background-color: #20A4F3;
  color: #fbfffe;
}

.SidebarList .ListItem {
  width: 100%;
  height: 50px;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  color:#fbfffe;
}

.SidebarList#title {
  margin-left: 15px;
  place-items: center;
}

.sidebarClose {
  animation: sidebarClose 1s forwards;
}

.sidebarOpen {
  animation: sidebarOpen 1s forwards;
}

@keyframes sidebarClose {
  0% {
    height: 100%;
    width: 15%;
  }
  100% {
    height: 60px;
    width: 60px;
  }
}
@keyframes sidebarOpen {
  0% {
    height: 60px;
    width: 60px;
  }
  100% {
    height: 100%;
    width: 15%;
  }
}